@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Work+Sans:wght@100;200;300;400;500;600;700;800;900&display=swap');

/*************************************************************************/
/* 01) Root
/*************************************************************************/
:root {
	--xoxo-bbc: #fff5cf;
	--xoxo-mc1: #ffcc00;
	--xoxo-mc2: #f16363;
	--xoxo-hc: #000;
	--xoxo-bc: #000;
	--hff: 'Montserrat', sans-serif;
	--bff: 'Work Sans', sans-serif;
}

/*************************************************************************/
/* 02) Body
/*************************************************************************/
html {
	padding: 0px;
	margin: 0px;
}

*:after,
*:before,
* {
	margin: 0;
	padding: 0;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
}

body {
	font-family: var(--bff);
	font-size: 18px;
	letter-spacing: 0;
	line-height: 1.5;
	font-weight: 400;
	color: var(--xoxo-bc);
	background-color: var(--xoxo-bbc);
	position: relative;

	-ms-word-wrap: break-word;
	word-wrap: break-word;
	word-break: break-word;
}

body,
html {
	width: 100%;
	margin: 0px;
	padding: 0px;
}

body {
	height: auto;
}

html:after,
html:before {
	clear: both;
	display: table;
	content: '';
}

html {
	overflow-x: hidden;
}

.fn__svg {
	fill: currentcolor;
	width: 18px;
	height: 18px;
}

.replaced-svg {
	opacity: 1;
}

::-webkit-input-placeholder {
	/* WebKit, Blink, Edge */
	color: #000;
}

:-moz-placeholder {
	/* Mozilla Firefox 4 to 18 */
	color: #000;
	opacity: 1;
}

::-moz-placeholder {
	/* Mozilla Firefox 19+ */
	color: #000;
	opacity: 1;
}

:-ms-input-placeholder {
	/* Internet Explorer 10-11 */
	color: #000;
}

::-ms-input-placeholder {
	/* Microsoft Edge */
	color: #000;
}

::placeholder {
	/* Most modern browsers support this now. */
	color: #000;
}

p {
	letter-spacing: 0;
	margin-bottom: 20px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	color: var(--xoxo-hc);
	font-family: var(--hff);
	line-height: 1.22;
	letter-spacing: 0;
}

input[type="password"],
input[type="email"],
input[type="tel"],
input[type="text"], 
input[type="number"] {
	background-color: transparent;
	letter-spacing: 0;
	outline: none;
	outline-color: transparent;
	border: 4px solid #000;
	border-radius: 30px;
	height: 60px;
	padding: 0 20px;
	font-size: 16px;
	font-weight: 500;
	font-family: var(--hff);
	color: #000;
}


textarea {
	width: 100%;
	max-width: 100%;
	min-width: 100%;
	display: block;
	border: 4px solid #000;
	border-radius: 30px;
	padding: 21px 26px;
	font-size: 18px;
	font-weight: 600;
	font-family: var(--hff);
	font-style: normal;
	color: #000;
}

textarea:focus,
input[type="email"]:focus,
input[type="tel"]:focus,
input[type="password"]:focus,
input[type="text"]:focus,
input[type="number"]:focus {
	outline: none;
	background-color: transparent;
	border-color: #000;
}

.container {
	max-width: 1460px;
	padding: 0 50px;
	margin: 0 auto;
	width: 100%;
}

.container .container {
	padding: 0;
}

@media(max-width: 1500px) {
	.container {
		max-width: 1360px;
	}
}

@media(max-width: 1040px) {
	.container {
		padding: 0 20px;
	}
}

@media(max-width: 480px) {
	.container {
		padding: 0 10px;
	}
}

/* COMMON */
.fn__svg {
	width: 20px;
	height: 20px;
	fill: currentcolor;
}

/*************************************************************************/
/* 03) Logo
/*************************************************************************/
.mobile_retina_logo,
.retina_logo {
	display: none;
}


.full_link {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
}


/* Hidden Infromation */
.xoxo_fn_hidden {
	display: none;
}



/*************************************************************************/
/* 04) Desktop Navigation
/*************************************************************************/
#xoxo_fn_header {
	max-width: 100%;
	width: 100%;
}

.xoxo_fn_header .header_top {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	align-items: center;
	justify-content: center;
	padding-top: 70px;
	padding-bottom: 30px;
	margin-bottom: 40px;
	position: relative;
	overflow: hidden;
}

.xoxo_fn_header .header_top:last-child {
	margin-bottom: 86px;
}

.xoxo_fn_header .header_top .wing_right:after,
.xoxo_fn_header .header_top .wing_right:before,
.xoxo_fn_header .header_top .wing_left:after,
.xoxo_fn_header .header_top .wing_left:before {
	position: absolute;
	content: '';
	width: 4px;
	background-color: #000;
	top: 0;
	display: block;
	pointer-events: none;
}

.xoxo_fn_header .header_top .wing_left:before {
	height: 60px;
	left: 0;
}

.xoxo_fn_header .header_top .wing_left:after {
	height: 40px;
	left: 14px;
}

.xoxo_fn_header .header_top .wing_right:before {
	height: 60px;
	right: 0;
}

.xoxo_fn_header .header_top .wing_right:after {
	height: 40px;
	right: 14px;
}

.xoxo_fn_header .header_top>* {
	min-width: 0;
}

.xoxo_fn_header .hide_me {
	opacity: 0;
	visibility: hidden;
	position: relative;
	z-index: -999999;
	pointer-events: none;
}

.xoxo_fn_header .logo {
	position: relative;
}

.xoxo_fn_header .logo a {
	padding: 0 70px;
	display: block;
	text-decoration: none;
}

.xoxo_fn_header .logo img {
	max-width: 300px;
	max-height: 300px;
}

.xoxo_fn_header .logo:after {
	content: '';
	position: absolute;
	bottom: -30px;
	left: 0;
	right: 0;
	background-image: url("../images/logo/bg.svg");
	background-repeat: repeat-x;
	height: 9px;
}

.xoxo_fn_header .search_opener a {
	display: block;
	text-decoration: none;
	position: relative;
	font-family: var(--hff);
	font-size: 18px;
	color: #000;
	font-weight: 900;
	text-transform: uppercase;
	line-height: 50px;
	padding-right: 30px;
}

.xoxo_fn_header .search_opener a:hover {
	-webkit-mask-image: linear-gradient(-75deg, rgba(0, 0, 0, .6) 30%, #000 50%, rgba(0, 0, 0, .6) 70%);
	-webkit-mask-size: 200%;
	animation: shine 2s infinite;
}

.xoxo_fn_header .search_opener .fn__svg {
	width: 20px;
	height: 20px;
	display: block;
	position: absolute;
	right: 0;
	top: 50%;
	margin-top: -11px;
}

.xoxo_fn_header .bottom_fixer {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	justify-content: center;
	max-width: 100%;
}

.xoxo_fn_header .xoxo_fn_nav {
	min-width: 800px;
	width: auto;
	max-width: 100%;
	margin: 0 auto;
	border: 4px solid #000;
	border-radius: 40px;
	position: relative;
	padding: 0 60px;
	transition: width .3s ease;
	background-color: #fff;
}

@media(max-width: 850px) {
	.xoxo_fn_header .xoxo_fn_nav {
		min-width: 100%;
	}
}

.xoxo_fn_header .xoxo_fn_nav:after {
	content: '';
	position: absolute;
	top: 0;
	left: -4px;
	right: -4px;
	bottom: -10px;
	z-index: -1;
	border-radius: 0 0 40px 40px;
	border-bottom: 10px solid #000;
}

.xoxo_fn_header .wing:before,
.xoxo_fn_header .wing:after {
	content: '';
	position: absolute;
	width: 20px;
	height: 4px;
	background-color: #000;
	top: 50%;
	margin-top: -2px;
}

.xoxo_fn_header .wing:before {
	left: 0;
}

.xoxo_fn_header .wing:after {
	right: 0;
}

.xoxo_fn_header .menu {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	justify-content: center;
	-ms-align-items: center;
	align-items: center;
}

.xoxo_fn_header .menu>div:first-child {
	max-width: 100%;
}

.xoxo_fn_header .header_bottom {
	padding-bottom: 86px;
}

.xoxo_fn_main_nav {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	margin: 0;
	padding: 0;
	list-style-type: none;
	margin-left: -34px;
}

.xoxo_fn_main_nav li {
	position: relative;
	margin: 0;
}

.icon_bar__share .xoxo_fn_social_list li,
.xoxo_fn_main_nav .sub-menu li {
	transform: translateY(-20px);
	transition: all .5s ease;
	opacity: 0;
}

.icon_bar__share:hover .xoxo_fn_social_list li,
.xoxo_fn_main_nav li:hover>.sub-menu>li {
	transform: translateY(0);
	opacity: 1;
}

.icon_bar__share .xoxo_fn_social_list li:nth-child(1),
.xoxo_fn_main_nav li:nth-child(1) {
	transition-delay: 0ms;
}

.icon_bar__share .xoxo_fn_social_list li:nth-child(2),
.xoxo_fn_main_nav li:nth-child(2) {
	transition-delay: 50ms;
}

.icon_bar__share .xoxo_fn_social_list li:nth-child(3),
.xoxo_fn_main_nav li:nth-child(3) {
	transition-delay: 100ms;
}

.icon_bar__share .xoxo_fn_social_list li:nth-child(4),
.xoxo_fn_main_nav li:nth-child(4) {
	transition-delay: 150ms;
}

.icon_bar__share .xoxo_fn_social_list li:nth-child(5),
.xoxo_fn_main_nav li:nth-child(5) {
	transition-delay: 200ms;
}

.icon_bar__share .xoxo_fn_social_list li:nth-child(6),
.xoxo_fn_main_nav li:nth-child(6) {
	transition-delay: 250ms;
}

.icon_bar__share .xoxo_fn_social_list li:nth-child(7),
.xoxo_fn_main_nav li:nth-child(7) {
	transition-delay: 300ms;
}

.icon_bar__share .xoxo_fn_social_list li:nth-child(8),
.xoxo_fn_main_nav li:nth-child(8) {
	transition-delay: 350ms;
}

.icon_bar__share .xoxo_fn_social_list li:nth-child(9),
.xoxo_fn_main_nav li:nth-child(9) {
	transition-delay: 400ms;
}

.icon_bar__share .xoxo_fn_social_list li:nth-child(10),
.xoxo_fn_main_nav li:nth-child(10) {
	transition-delay: 450ms;
}

.icon_bar__share .xoxo_fn_social_list li:nth-child(11),
.xoxo_fn_main_nav li:nth-child(11) {
	transition-delay: 500ms;
}

.icon_bar__share .xoxo_fn_social_list li:nth-child(12),
.xoxo_fn_main_nav li:nth-child(12) {
	transition-delay: 550ms;
}

.icon_bar__share .xoxo_fn_social_list li:nth-child(13),
.xoxo_fn_main_nav li:nth-child(13) {
	transition-delay: 600ms;
}

.icon_bar__share .xoxo_fn_social_list li:nth-child(14),
.xoxo_fn_main_nav li:nth-child(14) {
	transition-delay: 650ms;
}

.icon_bar__share .xoxo_fn_social_list li:nth-child(15),
.xoxo_fn_main_nav li:nth-child(15) {
	transition-delay: 700ms;
}

.icon_bar__share .xoxo_fn_social_list li:nth-child(16),
.xoxo_fn_main_nav li:nth-child(16) {
	transition-delay: 750ms;
}

.xoxo_fn_main_nav>li>a {
	white-space: nowrap;
}

.xoxo_fn_main_nav>li {
	margin-left: 34px;
}

.xoxo_fn_main_nav>li.disabled {
	display: none;
}

.xoxo_fn_header .more>a,
.xoxo_fn_stickynav .more>a,
.xoxo_fn_main_nav>li>a {
	font-size: 18px;
	display: block;
	text-decoration: none;
	color: #000;
	font-family: var(--hff);
	font-weight: 900;
	text-transform: uppercase;
	line-height: 62px;
}

.xoxo_fn_main_nav>li.menu-item-has-children>a:after {
	content: '';
	position: absolute;
	right: 0;
	top: 50%;
	margin-top: -4px;
	width: 0;
	height: 0;
	border-style: solid;
	border-width: 6px 6px 0 6px;
	border-color: #000 transparent transparent transparent;
}

.xoxo_fn_main_nav>li.menu-item-has-children>a {
	position: relative;
	padding-right: 16px;
}

.fn__meta a:hover,
.blog__link_item a:hover,
.xoxo_fn_footer .widget_nav_menu .menu a:hover,
#breadcrumbs a:hover,
.blog__item .author a:hover,
.blog__item .fn__meta a:hover,
.xoxo_fn_main_nav li:hover>a,
.comment #cancel-comment-reply-link:hover,
.xoxo_fn_stickynav .sub-menu a:hover,
.xoxo_fn_header .sub-menu a:hover,
.xoxo_fn_stickynav .more>a:hover,
.xoxo_fn_header .more>a:hover,
.xoxo_fn_footer .footer_copyright a:hover,
.xoxo_fn_main_nav>li>a:hover {
	color: var(--xoxo-mc2);
	-webkit-mask-image: linear-gradient(-75deg, rgba(0, 0, 0, .6) 30%, #000 50%, rgba(0, 0, 0, .6) 70%);
	-webkit-mask-size: 200%;
	animation: shine 2s infinite;
}

.xoxo_fn_main_nav>li>a .suffix {
	display: none;
}

.xoxo_fn_stickynav .more,
.xoxo_fn_header .more {
	display: none;
	position: relative;
	margin-left: 34px;
}

.xoxo_fn_stickynav .more.active,
.xoxo_fn_header .more.active {
	display: block;
	white-space: nowrap;
}

.xoxo_fn_stickynav .sub-menu li,
.xoxo_fn_header .sub-menu li {
	margin-bottom: 6px;
	position: relative;
}

.xoxo_fn_stickynav .sub-menu ul,
.xoxo_fn_header .sub-menu ul {
	left: 100%;
	top: -36px;
	margin-left: 0;
}

.xoxo_fn_stickynav .more .sub-menu ul,
.xoxo_fn_header .more .sub-menu ul {
	margin-right: 0;
	left: auto;
	right: 100%;
}

.xoxo_fn_stickynav .sub-menu,
.xoxo_fn_header .sub-menu {
	z-index: 5;
	opacity: 0;
	visibility: hidden;
	transition: all .3s ease;
	margin: 0;
	padding: 32px 0 25px;
	position: absolute;
	width: 300px;
	border: 4px solid #000;
	background-color: #fff;
	top: 100%;
	left: 0;
	margin-left: -44px;
	list-style-type: none;
	transform: translateY(20px);
	border-radius: 0 0 13px 13px;
}

.xoxo_fn_stickynav .sub-menu:after,
.xoxo_fn_header .sub-menu:after {
	content: '';
	position: absolute;
	top: 0;
	left: -4px;
	right: -4px;
	bottom: -10px;
	z-index: -1;
	border-radius: 0 0 20px 20px;
	border-bottom: 10px solid #000;
}

.xoxo_fn_stickynav .more .sub-menu,
.xoxo_fn_header .more .sub-menu {
	left: auto;
	right: 0;
	margin-left: 0;
	margin-right: -44px;
}

.xoxo_fn_stickynav .suffix,
.xoxo_fn_header .suffix {
	margin-left: 10px;
}

.xoxo_fn_stickynav .sub-menu a,
.xoxo_fn_header .sub-menu a {
	font-size: 18px;
	display: block;
	font-family: var(--hff);
	color: #000;
	font-weight: 900;
	letter-spacing: .25px;
	text-transform: uppercase;
	padding: 0 40px;
	line-height: 30px;
	white-space: normal;
}

.xoxo_fn_header .more li:hover>.sub-menu,
.xoxo_fn_header .more:hover>.sub-menu,
.xoxo_fn_stickynav .more li:hover>.sub-menu,
.xoxo_fn_stickynav .more:hover>.sub-menu,
.xoxo_fn_main_nav li:hover>.sub-menu {
	opacity: 1;
	visibility: visible;
	transform: translateY(0px);
}

.xoxo_fn_stickynav .sub-menu li.menu-item-has-children>a,
.xoxo_fn_header .sub-menu li.menu-item-has-children>a {
	padding-right: 52px;
	position: relative;
}

.xoxo_fn_stickynav .sub-menu li.menu-item-has-children>a:after,
.xoxo_fn_header .sub-menu li.menu-item-has-children>a:after {
	content: '';
	position: absolute;
	right: 40px;
	top: 50%;
	margin-top: -6px;
	display: block;
	width: 0;
	height: 0;
	border-style: solid;
	border-width: 6px 0 6px 6px;
	border-color: transparent transparent transparent #000;
}

.xoxo_fn_header .xoxo_fn_social_list ul {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	-ms-align-items: center;
	align-items: center;
	margin: 0;
	padding: 0;
	list-style-type: none;
	margin-left: -8px;
}

.xoxo_fn_header .xoxo_fn_social_list li {
	margin: 7px 0 7px 8px;
	padding: 0;
}

.xoxo_fn_header .xoxo_fn_social_list a {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	-ms-align-items: center;
	align-items: center;
	justify-content: center;
	color: #000;
	text-decoration: none;
	padding: 3px;
}

.xoxo_fn_header .xoxo_fn_social_list .fn__svg {
	width: 22px;
	height: 22px;
	display: block;
}

.xoxo_fn_header .xoxo_fn_social_list a:hover {
	-webkit-mask-image: linear-gradient(-75deg, rgba(255, 255, 255, .6) 30%, #fff 50%, rgba(255, 255, 255, .6) 70%);
	-webkit-mask-size: 200%;
	animation: shine 2s infinite;
	color: var(--xoxo-mc2);
}

/*************************************************************************/
/* 05) Searchbox
/*************************************************************************/
.xoxo_fn_searchbox {
	transition: all .5s ease;
	opacity: 0;
	visibility: hidden;
	transform: translateY(-100%) scale(0.5);
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: var(--xoxo-bbc);
	z-index: 10000;
	padding: 100px 0;
	height: 100vh;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	-ms-align-items: center;
	align-items: center;
}

.admin-bar .xoxo_fn_searchbox {
	top: 32px;
	height: calc(100vh - 32px);
}

.xoxo_fn_searchbox .container {
	max-width: 1460px;
}

.xoxo_fn_searchbox .input {
	flex: 1;
}

.xoxo_fn_searchbox .search {
	position: relative;
	background-color: var(--xoxo-mc1);
	border-radius: 70px;
	border: 4px solid #000;
	margin: -4px -4px -4px 0;
}

.xoxo_fn_searchbox .search:hover {
	background-color: #fff;
}

.xoxo_fn_searchbox .search:hover input {
	-webkit-mask-image: linear-gradient(-75deg, rgba(255, 255, 255, .6) 30%, #fff 50%, rgba(255, 255, 255, .6) 70%);
	-webkit-mask-size: 200%;
	animation: shine 2s infinite;
}

.xoxo_fn_searchbox .search .fn__svg {
	display: block;
	position: absolute;
	right: 50px;
	top: 50%;
	margin-top: -10px;
}

.xoxo_fn_searchbox .search input {
	display: block;
	position: relative;
	background-color: transparent !important;
	padding: 0 80px 0 50px;
	height: 72px;
	color: #000;
	font-size: 18px;
	font-family: var(--hff);
	text-transform: uppercase;
	letter-spacing: 0;
	font-weight: 900;
	z-index: 2;
}

.xoxo_fn_searchbox .input input {
	display: block;
	width: 100%;
	border: none;
	height: 72px;
	background-color: transparent !important;
	padding: 0 36px;
	font-size: 24px;
	font-style: normal;
	font-weight: 900;
	font-family: var(--hff);
}

.xoxo_fn_searchbox .search_content {
	height: calc(100vh - 200px);
	overflow-x: auto;
	padding: 0 20px;
	width: 100%;
}

.xoxo_fn_searchbox.ajax_result .search_content {
	display: block;
}

