/*************************************************************************/
/* 31) Magic Cursor
/*************************************************************************/
body.elementor-editor-active .frenify-cursor {
	display: none;
}

.frenify-cursor[data-default="no"] {
	opacity: 0;
}

.frenify-cursor[data-default="no"].cursor-inner {
	transition: opacity .3s ease;
}

.frenify-cursor[data-default="no"].cursor-inner.cursor-hover,
.frenify-cursor[data-default="no"].cursor-inner.cursor-slider {
	opacity: 1;
}

.frenify-cursor[data-link="no"].cursor-inner.cursor-hover {
	opacity: 0;
}

.frenify-cursor[data-slider="no"].cursor-inner.cursor-slider {
	opacity: 0;
}

.frenify-cursor {
	position: fixed;
	left: 0;
	top: 0;
	pointer-events: none;
	-webkit-transform: translateZ(0);
	transform: translateZ(0);
	visibility: hidden;
}

.frenify-cursor.mouse-down .fn-cursor {
	transform: scale(.9);
}

@media(max-width: 1040px) {
	.frenify-cursor {
		display: none;
	}
}

.cursor-link {
	width: 30px;
	height: 30px;
	margin-left: -15px;
	margin-top: -15px;
	border: 2px solid rgba(0, 0, 0, .5);
	z-index: 10000000;
	-webkit-transition: all .08s ease-out;
	transition: all .08s ease-out;
}

.cursor-outer {
	z-index: 10000000;
	-webkit-transition: transform .08s ease-out, opacity 1s ease;
	transition: transform .08s ease-out, opacity 1s ease;
}

.cursor-outer .fn-cursor {
	position: absolute;
	top: 50%;
	left: 50%;
	margin: -15px 0 0 -15px;
	width: 30px;
	height: 30px;
	border-radius: 50%;
	border: 2px solid rgba(241, 99, 99, 0.7);
}

.cursor-inner .fn-cursor {
	position: absolute;
	top: 50%;
	left: 50%;
	margin: -3px 0 0 -3px;
	width: 6px;
	height: 6px;
	border-radius: 50%;
	background-color: rgba(241, 99, 99, 0.7);
	-webkit-transition: all .5s ease-in-out;
	transition: all .5s ease-in-out;
}

.cursor-outer.cursor-slider,
.cursor-outer.cursor-hover {
	opacity: 0;
	-webkit-transition: transform .08s ease-out, opacity .2s ease;
	transition: transform .08s ease-out, opacity .2s ease;
}

.cursor-inner {
	z-index: 10000001;
}

.cursor-inner.cursor-hover .fn-cursor {
	margin: -40px 0 0 -40px;
	width: 80px;
	height: 80px;
	background-color: rgba(255, 202, 202, 0.7);
}

.cursor-inner.cursor-slider:not(.cursor-hover) .fn-cursor {
	margin-left: -30px;
	margin-top: -30px;
	width: 60px;
	height: 60px;
	background-color: transparent;
	border: 5px solid rgba(241, 99, 99, 0.7);
}

.cursor-inner.cursor-slider.cursor-hover span:after,
.cursor-inner.cursor-slider.cursor-hover span:before {
	background-color: #fff;
}

.cursor-inner.cursor-slider .fn-left,
.cursor-inner.cursor-slider .fn-right {
	opacity: 1;
	visibility: visible;
}

.cursor-inner.cursor-slider .fn-left:after {
	left: -26px;
}

.cursor-inner.cursor-slider .fn-left:before {
	left: -26px;
}

.cursor-inner.cursor-slider .fn-left:after {
	right: -26px;
}

.cursor-inner.cursor-slider .fn-left:before {
	right: -26px;
}

.cursor-inner .fn-left,
.cursor-inner .fn-right {
	opacity: 0;
	visibility: hidden;
	transition: all .3s ease;
}

.cursor-inner .fn-left:after {
	content: '';
	position: absolute;
	width: 2px;
	height: 12px;
	transform: rotate(50deg);
	left: -20px;
	top: 50%;
	margin-top: -10px;
	background-color: rgba(241, 99, 99, 0.7);
	transition: all .3s ease;
}

.cursor-inner .fn-left:before {
	content: '';
	position: absolute;
	width: 2px;
	height: 12px;
	transform: rotate(130deg);
	left: -20px;
	top: 50%;
	margin-top: -2px;
	background-color: rgba(241, 99, 99, 0.7);
	transition: all .3s ease;
}

.cursor-inner .fn-right:after {
	content: '';
	position: absolute;
	width: 2px;
	height: 12px;
	transform: rotate(50deg);
	right: -20px;
	top: 50%;
	margin-top: -2px;
	background-color: rgba(241, 99, 99, 0.7);
	transition: all .3s ease;
}

.cursor-inner .fn-right:before {
	content: '';
	position: absolute;
	width: 2px;
	height: 12px;
	transform: rotate(130deg);
	right: -20px;
	top: 50%;
	margin-top: -10px;
	background-color: rgba(241, 99, 99, 0.7);
	transition: all .3s ease;
}

