.fn__bp_slider {
	.swiper-container {
		.swiper-slide {
			overflow: hidden;
		}
	}
}
.ls_content{
	border: 4px solid #000;
	border-bottom: 10px solid;
    border-radius: 40px;
    position: relative;
    background-color: #fff;
    padding: 70px 50px;
	&.four{
		border: none !important;
		background: none;
		padding: 0;
	}
}
.block-gallery{
	display: flex;
	flex-wrap: wrap;
}
.block-image{
	display: flex;
    flex-grow: 1;
    justify-content: center;
    position: relative;
    flex-direction: column;
    max-width: 100%;
    box-sizing: border-box;
	width: 48%;
	margin: 0 10px 10px 0;
	img{
		width: 100%;
		flex: 1 0 0%;
		height: 100%;
		object-fit: cover;
		display: block;
	}
	&.size-full{
		width: 100%;
		img{
			width: 100% !important;
		}
	}
}
.blocks-gallery-caption{
	span{
		color: rgb(2, 2, 2);
		font-family: goudy-old-style, Garamond, "Times New Roman", serif;
		font-size: 25px;
		text-align: left;
		white-space: normal;
	}
}
.post_top_format{
	li{
		list-style-type: none;
	}
}
.small-img{
	width: 320px;
}
form{
	&.contact-form{

		input {
			margin-bottom: 20px;
			height: 60px;
			width: 49%;
			font-weight: 600 !important;
			font-size: 18px;
			color: #000;
			font-family: var(--hff);
			@media(max-width:700px){
				width: 100%;
			}
			&::placeholder{
				color: #000;
				font-family: var(--hff);
				font-weight: 600 !important;
				font-size: 18px;
			}
		}
		textarea{
			margin-bottom: 20px;
			height: 250px;
		}
		button{
			width: 100%;
			height: 60px;
			color: #000;
			font-family: var(--hff);
			font-size: 18px;
			font-weight: 900;
			border-radius: 30px;
			background: var(--xoxo-mc1);
			display: block;
			border: 4px solid #000;
			cursor: pointer;
			text-transform: uppercase;
			transition: all .4s;
			margin-bottom: 20px;
			@media(max-width:450px){
				font-size: 16px;
			}
			&:hover{
				-webkit-mask-image: linear-gradient(-75deg, rgba(0,0,0,.6) 30%, #000 50%, rgba(0,0,0,.6) 70%);
				-webkit-mask-size: 200%;
				animation: shine 2s infinite;
				background: #fff;
			}
		}
	}
}
.ls_content{
	@media(max-width:450px){
		padding: 70px 20px;
	}
}